body {
  background-color: #03aa94e0;
  padding: 0px;
  margin: 0;
}
h1, h2, p, ul, li {
  font-family: 'Lucida Sans',  Geneva, Verdana, sans-serif;
}
h1{
  text-align: center;
  
}
h2{
  text-align: center;
  
}
h3{
  text-align: center;
  
}
ul.header li {
  display: inline;
  list-style-type: none;
  margin: 0px;
  padding: 0px;

}
ul.header {
  background-color: rgb(65, 67, 184);
  padding: 0;
  margin: auto;
}
ul.header li a {
  color: #FFF;
  font-weight: bold;
  text-decoration: none;
  padding: 20px;
  display: inline-block;
}
.content {
  background-color: #FFF;
  padding: 20px;
}
.content h2 {
  padding: 0;
  margin: 0;
}
.content li {
  margin-bottom: 10px;
}
.active {
  background-color: #000000;
  
}
ul.header li a:hover{
  background-color: #e09f13;
}

.footer {
  position:fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: rgb(0, 0, 0);
  color: white;
  text-align: center;
}
